import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { CalendarMemberRole } from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/calendars/[calendarId]/members
// Get all members of a calendar (for team/round-robin calendars)
// ==========================================
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin

    // Get calendar to verify type
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select('id, calendar_type, name')
      .eq('id', calendarId)
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    // Get members with user details
    const { data: members, error } = await supabase
      .from('calendar_members')
      .select(`
        *,
        users:user_id (id, name, email, avatar_url)
      `)
      .eq('calendar_id', calendarId)
      .order('priority_order', { ascending: true })

    if (error) {
      console.error('Error fetching members:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch members' },
        { status: 500 }
      )
    }

    // Transform data
    const transformedMembers = (members || []).map((m: any) => ({
      id: m.id,
      calendarId: m.calendar_id,
      userId: m.user_id,
      role: m.role,
      isPrimaryOwner: m.is_primary_owner,
      priorityOrder: m.priority_order,
      weight: m.weight,
      isActive: m.is_active,
      canEditCalendar: m.can_edit_calendar,
      canManageAppointments: m.can_manage_appointments,
      createdAt: m.created_at,
      user: m.users ? {
        id: m.users.id,
        name: m.users.name,
        email: m.users.email,
        avatar: m.users.avatar_url
      } : null
    }))

    // Get round-robin assignment stats if applicable
    let roundRobinStats = null
    if (calendar.calendar_type === 'round_robin') {
      const { data: rrAssignment } = await supabase
        .from('round_robin_assignments')
        .select('*')
        .eq('calendar_id', calendarId)
        .single()

      if (rrAssignment) {
        roundRobinStats = {
          lastAssignedUserId: rrAssignment.last_assigned_user_id,
          lastAssignedAt: rrAssignment.last_assigned_at,
          assignmentCounts: rrAssignment.assignment_counts || {},
          periodStart: rrAssignment.period_start,
          periodEnd: rrAssignment.period_end
        }
      }
    }

    return NextResponse.json({
      success: true,
      data: {
        calendarId,
        calendarType: calendar.calendar_type,
        calendarName: calendar.name,
        members: transformedMembers,
        roundRobinStats,
        totalMembers: transformedMembers.length,
        activeMembers: transformedMembers.filter((m: any) => m.isActive).length
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/calendars/[calendarId]/members:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/calendars/[calendarId]/members
// Add a new member to calendar
// ==========================================
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      userId,
      email,  // Alternative: find user by email
      role = 'member',
      isPrimaryOwner = false,
      priorityOrder,
      weight = 1,
      isActive = true,
      canEditCalendar = false,
      canManageAppointments = true
    } = body

    // Validate role
    const validRoles: CalendarMemberRole[] = ['owner', 'member', 'viewer']
    if (!validRoles.includes(role)) {
      return NextResponse.json(
        { success: false, error: `Invalid role. Must be one of: ${validRoles.join(', ')}` },
        { status: 400 }
      )
    }

    // Find user ID if email provided
    let finalUserId = userId
    if (!finalUserId && email) {
      const { data: user } = await supabase
        .from('users')
        .select('id')
        .eq('email', email)
        .single()

      if (!user) {
        return NextResponse.json(
          { success: false, error: 'User not found with that email' },
          { status: 404 }
        )
      }
      finalUserId = user.id
    }

    if (!finalUserId) {
      return NextResponse.json(
        { success: false, error: 'userId or email is required' },
        { status: 400 }
      )
    }

    // Check if user is already a member
    const { data: existingMember } = await supabase
      .from('calendar_members')
      .select('id')
      .eq('calendar_id', calendarId)
      .eq('user_id', finalUserId)
      .single()

    if (existingMember) {
      return NextResponse.json(
        { success: false, error: 'User is already a member of this calendar' },
        { status: 409 }
      )
    }

    // Get highest priority order if not provided
    let finalPriorityOrder = priorityOrder
    if (finalPriorityOrder === undefined) {
      const { data: lastMember } = await supabase
        .from('calendar_members')
        .select('priority_order')
        .eq('calendar_id', calendarId)
        .order('priority_order', { ascending: false })
        .limit(1)
        .single()

      finalPriorityOrder = (lastMember?.priority_order || 0) + 1
    }

    // Create member
    const memberId = uuidv4()
    const { data: member, error } = await supabase
      .from('calendar_members')
      .insert({
        id: memberId,
        calendar_id: calendarId,
        user_id: finalUserId,
        role,
        is_primary_owner: isPrimaryOwner,
        priority_order: finalPriorityOrder,
        weight,
        is_active: isActive,
        can_edit_calendar: canEditCalendar,
        can_manage_appointments: canManageAppointments
      })
      .select(`
        *,
        users:user_id (id, name, email, avatar_url)
      `)
      .single()

    if (error) {
      console.error('Error creating member:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to add member' },
        { status: 500 }
      )
    }

    // Initialize round-robin assignment if this is first member of round-robin calendar
    const { data: calendar } = await supabase
      .from('calendars')
      .select('calendar_type')
      .eq('id', calendarId)
      .single()

    if (calendar?.calendar_type === 'round_robin') {
      const { data: rrAssignment } = await supabase
        .from('round_robin_assignments')
        .select('id')
        .eq('calendar_id', calendarId)
        .single()

      if (!rrAssignment) {
        await supabase
          .from('round_robin_assignments')
          .insert({
            calendar_id: calendarId,
            assignment_counts: {}
          })
      }
    }

    return NextResponse.json({
      success: true,
      data: {
        id: member.id,
        calendarId: member.calendar_id,
        userId: member.user_id,
        role: member.role,
        isPrimaryOwner: member.is_primary_owner,
        priorityOrder: member.priority_order,
        weight: member.weight,
        isActive: member.is_active,
        canEditCalendar: member.can_edit_calendar,
        canManageAppointments: member.can_manage_appointments,
        createdAt: member.created_at,
        user: member.users ? {
          id: member.users.id,
          name: member.users.name,
          email: member.users.email,
          avatar: member.users.avatar_url
        } : null
      }
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/calendars/[calendarId]/members:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/calendars/[calendarId]/members
// Update a member's settings
// ==========================================
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      memberId,
      userId,  // Alternative identifier
      role,
      isPrimaryOwner,
      priorityOrder,
      weight,
      isActive,
      canEditCalendar,
      canManageAppointments
    } = body

    if (!memberId && !userId) {
      return NextResponse.json(
        { success: false, error: 'memberId or userId is required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {}

    if (role !== undefined) {
      const validRoles: CalendarMemberRole[] = ['owner', 'member', 'viewer']
      if (!validRoles.includes(role)) {
        return NextResponse.json(
          { success: false, error: `Invalid role. Must be one of: ${validRoles.join(', ')}` },
          { status: 400 }
        )
      }
      updates.role = role
    }

    if (isPrimaryOwner !== undefined) updates.is_primary_owner = isPrimaryOwner
    if (priorityOrder !== undefined) updates.priority_order = priorityOrder
    if (weight !== undefined) updates.weight = weight
    if (isActive !== undefined) updates.is_active = isActive
    if (canEditCalendar !== undefined) updates.can_edit_calendar = canEditCalendar
    if (canManageAppointments !== undefined) updates.can_manage_appointments = canManageAppointments

    if (Object.keys(updates).length === 0) {
      return NextResponse.json(
        { success: false, error: 'No fields to update' },
        { status: 400 }
      )
    }

    // Build query
    let query = supabase
      .from('calendar_members')
      .update(updates)
      .eq('calendar_id', calendarId)

    if (memberId) {
      query = query.eq('id', memberId)
    } else if (userId) {
      query = query.eq('user_id', userId)
    }

    const { data: member, error } = await query
      .select(`
        *,
        users:user_id (id, name, email, avatar_url)
      `)
      .single()

    if (error || !member) {
      console.error('Error updating member:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update member or member not found' },
        { status: error ? 500 : 404 }
      )
    }

    return NextResponse.json({
      success: true,
      data: {
        id: member.id,
        calendarId: member.calendar_id,
        userId: member.user_id,
        role: member.role,
        isPrimaryOwner: member.is_primary_owner,
        priorityOrder: member.priority_order,
        weight: member.weight,
        isActive: member.is_active,
        canEditCalendar: member.can_edit_calendar,
        canManageAppointments: member.can_manage_appointments,
        createdAt: member.created_at,
        user: member.users ? {
          id: member.users.id,
          name: member.users.name,
          email: member.users.email,
          avatar: member.users.avatar_url
        } : null
      }
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/calendars/[calendarId]/members:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/calendars/[calendarId]/members
// Remove a member from calendar
// ==========================================
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const memberId = searchParams.get('memberId')
    const userId = searchParams.get('userId')

    if (!memberId && !userId) {
      return NextResponse.json(
        { success: false, error: 'memberId or userId is required' },
        { status: 400 }
      )
    }

    // Check if this is the primary owner
    let query = supabase
      .from('calendar_members')
      .select('is_primary_owner')
      .eq('calendar_id', calendarId)

    if (memberId) {
      query = query.eq('id', memberId)
    } else if (userId) {
      query = query.eq('user_id', userId)
    }

    const { data: member } = await query.single()

    if (member?.is_primary_owner) {
      return NextResponse.json(
        { success: false, error: 'Cannot remove the primary owner. Transfer ownership first.' },
        { status: 400 }
      )
    }

    // Delete member
    let deleteQuery = supabase
      .from('calendar_members')
      .delete()
      .eq('calendar_id', calendarId)

    if (memberId) {
      deleteQuery = deleteQuery.eq('id', memberId)
    } else if (userId) {
      deleteQuery = deleteQuery.eq('user_id', userId)
    }

    const { error } = await deleteQuery

    if (error) {
      console.error('Error removing member:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to remove member' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      message: 'Member removed successfully'
    })

  } catch (error) {
    console.error('Error in DELETE /api/v1/calendars/[calendarId]/members:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PATCH /api/v1/calendars/[calendarId]/members
// Bulk update member order (for drag-and-drop reordering)
// ==========================================
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const { memberOrder } = body // Array of { memberId, priorityOrder }

    if (!memberOrder || !Array.isArray(memberOrder)) {
      return NextResponse.json(
        { success: false, error: 'memberOrder array is required' },
        { status: 400 }
      )
    }

    // Update each member's priority order
    const updatePromises = memberOrder.map(({ memberId, priorityOrder }: { memberId: string; priorityOrder: number }) =>
      supabase
        .from('calendar_members')
        .update({ priority_order: priorityOrder })
        .eq('id', memberId)
        .eq('calendar_id', calendarId)
    )

    await Promise.all(updatePromises)

    return NextResponse.json({
      success: true,
      message: 'Member order updated successfully'
    })

  } catch (error) {
    console.error('Error in PATCH /api/v1/calendars/[calendarId]/members:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}
